'use client';
import React from 'react';
import { 
  Search, 
  RotateCcw, 
  FileText, 
  Compass, 
  FolderOpen, 
  Sparkles, 
  Settings, 
  HelpCircle,
  MessageCircle,
  Plus
} from 'lucide-react';
import useStore from '@/store/useStore';

const Sidebar = () => {
  const { undo, redo, saveWorkflow, loadWorkflow } = useStore();

  const onDragStart = (event: React.DragEvent, nodeType: string) => {
    event.dataTransfer.setData('application/reactflow', nodeType);
    event.dataTransfer.effectAllowed = 'move';
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (evt) => {
      try {
        const flow = JSON.parse(evt.target?.result as string);
        loadWorkflow(flow);
      } catch (err) {
        console.error("Failed to parse workflow", err);
        alert("Invalid workflow file");
      }
    };
    reader.readAsText(file);
  };

  const handleSave = () => {
    saveWorkflow();
    const { nodes, edges } = useStore.getState();
    const flow = { nodes, edges };
    const blob = new Blob([JSON.stringify(flow, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `galaxy-workflow-${Date.now()}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const menuItems = [
    { icon: Search, label: 'Search', action: () => {} },
    { icon: RotateCcw, label: 'Undo', action: undo },
    { icon: FileText, label: 'Templates', action: () => {} },
    { icon: Compass, label: 'Explore', action: () => {} },
    { icon: FolderOpen, label: 'Projects', action: () => {} },
    { icon: Sparkles, label: 'AI Generate', action: () => {} },
  ];

  const bottomItems = [
    { icon: Settings, label: 'Settings', action: () => {} },
    { icon: HelpCircle, label: 'Help', action: () => {} },
    { icon: MessageCircle, label: 'Feedback', action: () => {} },
  ];

  const nodeTypes = [
    { type: 'textNode', icon: 'üìù', label: 'Text' },
    { type: 'imageNode', icon: 'üñºÔ∏è', label: 'Image' },
    { type: 'llmNode', icon: 'ü§ñ', label: 'LLM' },
  ];

  return (
    <aside className="w-12 h-screen bg-[#1a1a1e] border-r border-[#2a2a2e] flex flex-col items-center py-3 z-20">
      {/* Top Menu */}
      <div className="flex flex-col items-center gap-1">
        {menuItems.map((item, index) => (
          <button
            key={index}
            onClick={item.action}
            className="w-9 h-9 flex items-center justify-center rounded-lg text-gray-500 hover:text-gray-300 hover:bg-[#2a2a2e] transition-all"
            title={item.label}
          >
            <item.icon size={18} />
          </button>
        ))}
      </div>

      {/* Divider */}
      <div className="w-6 h-px bg-[#3a3a3e] my-3" />

      {/* Draggable Node Types */}
      <div className="flex flex-col items-center gap-1">
        {nodeTypes.map((node, index) => (
          <div
            key={index}
            draggable
            onDragStart={(e) => onDragStart(e, node.type)}
            className="w-9 h-9 flex items-center justify-center rounded-lg text-gray-500 hover:text-gray-300 hover:bg-[#2a2a2e] transition-all cursor-grab active:cursor-grabbing"
            title={`Drag to add ${node.label} Node`}
          >
            <span className="text-base">{node.icon}</span>
          </div>
        ))}
      </div>

      {/* Spacer */}
      <div className="flex-1" />

      {/* Bottom Menu */}
      <div className="flex flex-col items-center gap-1">
        {/* Load button */}
        <label
          className="w-9 h-9 flex items-center justify-center rounded-lg text-gray-500 hover:text-gray-300 hover:bg-[#2a2a2e] transition-all cursor-pointer"
          title="Load Workflow"
        >
          <FolderOpen size={18} />
          <input 
            type="file" 
            className="hidden" 
            accept=".json" 
            onChange={handleFileUpload} 
          />
        </label>
        
        {bottomItems.map((item, index) => (
          <button
            key={index}
            onClick={item.action}
            className="w-9 h-9 flex items-center justify-center rounded-lg text-gray-500 hover:text-gray-300 hover:bg-[#2a2a2e] transition-all"
            title={item.label}
          >
            <item.icon size={18} />
          </button>
        ))}
      </div>
    </aside>
  );
};

export default Sidebar;
