'use client';
import { memo, useCallback, useState } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import { MoreHorizontal } from 'lucide-react';
import useStore from '@/store/useStore';
import { TextNodeData } from '@/types/workflow';

const TextNode = ({ id, data, selected }: NodeProps<TextNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);

  const handleTextChange = useCallback(
    (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      updateNodeData(id, { text: e.target.value });
    },
    [id, updateNodeData]
  );

  const handleLabelChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      updateNodeData(id, { label: e.target.value });
    },
    [id, updateNodeData]
  );

  return (
    <div
      className={`
        min-w-[280px] max-w-[320px] 
        bg-[#f5f0e6] 
        rounded-xl shadow-lg
        border transition-all duration-200
        ${selected 
          ? 'border-[#e84393] shadow-[#e84393]/20 shadow-xl' 
          : 'border-[#e8e0d0] hover:border-[#e84393]/50'
        }
      `}
    >
      {/* Header */}
      <div className="px-4 py-3 flex items-center justify-between">
        <input
          type="text"
          value={data.label || 'Prompt'}
          onChange={handleLabelChange}
          className="text-sm font-medium bg-transparent border-none outline-none text-gray-700 placeholder-gray-400"
          placeholder="Prompt"
        />
        <button className="p-1 hover:bg-black/5 rounded transition-colors">
          <MoreHorizontal size={16} className="text-gray-500" />
        </button>
      </div>

      {/* Content */}
      <div className="px-4 pb-4">
        <textarea
          value={data.text || ''}
          onChange={handleTextChange}
          placeholder="Enter text content..."
          className="
            w-full min-h-[100px] max-h-[200px]
            p-0 text-sm
            bg-transparent
            border-none
            focus:outline-none focus:ring-0
            text-gray-700
            placeholder-gray-400
            resize-none
            leading-relaxed
          "
        />
      </div>

      {/* Handles */}
      <Handle
        type="source"
        position={Position.Right}
        id="source"
        className="!w-3 !h-3 !bg-[#e84393] !border-2 !border-[#1a1a1e]"
      />
      <Handle
        type="target"
        position={Position.Left}
        id="target"
        className="!w-3 !h-3 !bg-[#e84393] !border-2 !border-[#1a1a1e]"
      />
    </div>
  );
};

export default memo(TextNode);
