'use client';
import { memo, useCallback, useRef } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import { Download, MoreHorizontal } from 'lucide-react';
import useStore from '@/store/useStore';
import { ImageNodeData } from '@/types/workflow';

const ImageNode = ({ id, data, selected }: NodeProps<ImageNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleLabelChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      updateNodeData(id, { label: e.target.value });
    },
    [id, updateNodeData]
  );

  const handleFileUpload = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const file = e.target.files?.[0];
      if (!file) return;

      if (!file.type.startsWith('image/')) {
        alert('Please upload an image file');
        return;
      }

      const reader = new FileReader();
      reader.onload = (evt) => {
        const imageData = evt.target?.result as string;
        updateNodeData(id, { 
          imageData, 
          fileName: file.name 
        });
      };
      reader.readAsDataURL(file);
    },
    [id, updateNodeData]
  );

  const handleRemoveImage = useCallback(() => {
    updateNodeData(id, { imageData: undefined, fileName: undefined });
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  }, [id, updateNodeData]);

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleExport = () => {
    // Export functionality - could download the connected output
    console.log('Export triggered');
  };

  return (
    <div
      className={`
        min-w-[180px] max-w-[220px] 
        bg-[#2a2a2e] 
        rounded-xl shadow-lg
        border transition-all duration-200
        ${selected 
          ? 'border-[#e84393] shadow-[#e84393]/20 shadow-xl' 
          : 'border-[#3a3a3e] hover:border-[#e84393]/50'
        }
      `}
    >
      {/* Header */}
      <div className="px-4 py-3 flex items-center justify-between">
        <input
          type="text"
          value={data.label || 'Export'}
          onChange={handleLabelChange}
          className="text-sm font-medium bg-transparent border-none outline-none text-gray-300 placeholder-gray-500 flex-1"
          placeholder="Export"
        />
        <button className="p-1 hover:bg-white/10 rounded transition-colors">
          <MoreHorizontal size={16} className="text-gray-500" />
        </button>
      </div>

      {/* Content */}
      <div className="px-4 pb-4">
        {data.imageData ? (
          <div className="relative group">
            <img
              src={data.imageData}
              alt={data.fileName || 'Uploaded image'}
              className="w-full h-auto max-h-[120px] object-contain rounded-lg border border-[#3a3a3e]"
            />
            <button
              onClick={handleRemoveImage}
              className="absolute top-2 right-2 p-1.5 bg-red-500 text-white rounded-full opacity-0 group-hover:opacity-100 transition-opacity shadow-lg hover:bg-red-600"
              title="Remove image"
            >
              √ó
            </button>
          </div>
        ) : (
          <button
            onClick={handleExport}
            className="
              w-full py-3 px-4
              border border-dashed border-[#4a4a4e]
              rounded-lg
              hover:border-[#e84393] hover:bg-[#e84393]/5
              transition-all duration-200
              flex items-center justify-center gap-2
            "
          >
            <Download size={18} className="text-gray-400" />
          </button>
        )}
        
        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleFileUpload}
          className="hidden"
        />
      </div>

      {/* Handles */}
      <Handle
        type="source"
        position={Position.Right}
        id="source"
        className="!w-3 !h-3 !bg-[#e84393] !border-2 !border-[#1a1a1e]"
      />
      <Handle
        type="target"
        position={Position.Left}
        id="target"
        className="!w-3 !h-3 !bg-[#e84393] !border-2 !border-[#1a1a1e]"
      />
    </div>
  );
};

export default memo(ImageNode);
